.video_main {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /*max-width: 100vw;*/
    max-height: 100vh;

    height:100%;
    aspect-ratio: 16/9;
}

.testBox {
    background-color: black;
    display: flex;
    opacity: 0.75;
    color: white;
    font-size: 60px;

    padding: 24px;
    width: 50%;
    justify-content: center;

    border-radius: 20px;
}

.parallax_wrapper {
    display:flex;
    justify-content: center;
}

.blink {
    animation: blink-animation 1.9s linear infinite;
}

@keyframes blink-animation {
    0% {
     opacity: 0.5;
    }
    50% {
     opacity: 1;
    }
    100% {
     opacity: 0.5;
    }
}